/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
	color: #404040;
	font-family: "Open Sans", Helvetica, Arial, sans-serif;
	font-size: 1rem;
	line-height: 1.8;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
	margin: 0.5em 0;
}

h1 {
	font-size: 2em;
}
@media only screen and (min-width: 600px) {
	h1 {
		font-size: 2.5em;
	}
}

h2 {
	font-size: 1.5em;
}
@media only screen and (min-width: 600px) {
	h2 {
		font-size: 2em;
	}
}

h3,
h4 {
	font-size: 1.25em;
}
@media only screen and (min-width: 600px) {
	h3,
	h4 {
		font-size: 1.5em;
	}
}

p {
	margin: 0 0 1.5em;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", courier, monospace;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/* Elements
--------------------------------------------- */
body {
	background: #fce2e2;
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
	padding: 0;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
}

figure {
	margin: 1em 0;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

.schedule {
	border-collapse: collapse;
}

.schedule tr {
	background-color: #eee;
}

.schedule tbody tr:nth-of-type(odd) {
	background-color: #fff;
}

.schedule th {
	text-align: left;
}

.schedule th,
.schedule td {
	display: block;
}

.schedule tr {
	display: block;
	padding: 1rem;
}

@media only screen and (min-width: 37.5em) {
	.schedule th,
	.schedule td {
		display: table-cell;
		width: 33.33%;
		padding: 2rem;
	}
	.schedule tr {
		display: table-row;
	}
}
/* Links
--------------------------------------------- */
a {
	color: #003c71;
	text-decoration: none;
	font-weight: 700;
}
a:visited {
	color: #003c71;
}
a:hover, a:focus, a:active {
	color: #305c9f;
	text-decoration: underline;
}
a:hover, a:active {
	outline: 0;
}

/* Forms
--------------------------------------------- */
button,
input[type=button],
input[type=reset],
input[type=submit] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	line-height: 1;
	padding: 0.6em 1em 0.4em;
}
button:hover,
input[type=button]:hover,
input[type=reset]:hover,
input[type=submit]:hover {
	border-color: #ccc #bbb #aaa;
}
button:active, button:focus,
input[type=button]:active,
input[type=button]:focus,
input[type=reset]:active,
input[type=reset]:focus,
input[type=submit]:active,
input[type=submit]:focus {
	border-color: #aaa #bbb #bbb;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=range]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=time]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=color]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
.site {
	max-width: 2200px;
	margin: 0 auto;
	overflow: hidden;
	display: grid;
	grid-template-columns: 1fr minmax(0, 1000px) 1fr;
	grid-template-areas: "header header header" ". main ." ". sidebar ." "footer footer footer";
}

.site-header {
	grid-area: header;
}

.site-main {
	grid-area: main;
}

.widget-area {
	grid-area: sidebar;
}

.site-footer {
	grid-area: footer;
}

.no-sidebar .site {
	display: grid;
	grid-template-columns: auto;
	grid-template-areas: "header" "main" "footer";
}

/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
/* Navigation
--------------------------------------------- */
.main-navigation {
	display: block;
	width: 100%;
	position: absolute;
	top: 6.5rem;
	right: 0;
	z-index: 1000;
}

.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
	background-color: #333;
}

.main-navigation li {
	position: relative;
}

.main-navigation li.current-menu-item > a,
.main-navigation li.current_page_item > a,
.main-navigation li:hover > a,
.main-navigation li.focus > a {
	text-decoration: underline;
	text-decoration-color: #dabbff;
}

.main-navigation li.current-menu-item svg path,
.main-navigation li.current_page_item svg path,
.main-navigation li:hover > a svg path,
.main-navigation li.focus > a svg path {
	fill: #fff200;
}

.main-navigation a {
	display: block;
	padding: 0.75rem 1.25rem;
	text-align: center;
	color: #fff;
}
.main-navigation a:visited {
	color: #fff;
}

.main-navigation.toggled ul {
	display: block;
}

.menu-toggle {
	position: absolute;
	top: -3.5rem;
	right: 0.5rem;
	z-index: 1100;
	display: block;
	width: 3.75rem;
	padding: 1rem;
	margin: 0;
	border: none;
	background: transparent;
	color: #fff;
}

@media screen and (min-width: 50em) {
	.menu-toggle {
		display: none;
	}
	.main-navigation {
		width: auto;
		position: relative;
		top: 0;
		align-self: center;
		margin-left: auto;
	}
	.main-navigation ul,
	.main-navigation.toggled ul {
		display: flex;
		justify-content: center;
	}
	.main-navigation ul ul {
		float: left;
		position: absolute;
		top: 100%;
		left: -999em;
		z-index: 99999;
		flex-direction: column;
	}
	.main-navigation ul ul a {
		left: -999em;
		top: 0;
		width: 200px;
		text-align: left;
	}
	.main-navigation ul ul li:hover > ul,
	.main-navigation ul ul li.focus > ul {
		left: 100%;
		top: 0;
	}
	.main-navigation ul li:hover > ul,
	.main-navigation ul li.focus > ul {
		left: auto;
	}
}
/* Posts and pages
--------------------------------------------- */
.sticky {
	display: block;
}

.post,
.page {
	margin: 0 0 1.5em;
}

.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

/* Comments
--------------------------------------------- */
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/* Widgets
--------------------------------------------- */
.widget {
	margin: 0 0 1.5em;
}
.widget select {
	max-width: 100%;
}

.widget-title {
	font-size: 1.25rem;
}

/* Media
--------------------------------------------- */
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/* Captions
--------------------------------------------- */
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}
.wp-caption img[class*=wp-image-] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}
.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/* Galleries
--------------------------------------------- */
.gallery {
	margin-bottom: 1.5em;
	display: grid;
	grid-gap: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	width: 100%;
}

.gallery-columns-2 {
	grid-template-columns: repeat(2, 1fr);
}

.gallery-columns-3 {
	grid-template-columns: repeat(3, 1fr);
}

.gallery-columns-4 {
	grid-template-columns: repeat(4, 1fr);
}

.gallery-columns-5 {
	grid-template-columns: repeat(5, 1fr);
}

.gallery-columns-6 {
	grid-template-columns: repeat(6, 1fr);
}

.gallery-columns-7 {
	grid-template-columns: repeat(7, 1fr);
}

.gallery-columns-8 {
	grid-template-columns: repeat(8, 1fr);
}

.gallery-columns-9 {
	grid-template-columns: repeat(9, 1fr);
}

.gallery-caption {
	display: block;
}

/* Header
--------------------------------------------- */
/* Header styles */
.site-header {
	background-color: #333;
	color: #fff;
	padding: 0 20px;
}
@media screen and (min-width: 50em) {
	.site-header {
		display: grid;
		grid-template-columns: 1fr minmax(auto, 25rem) minmax(auto, 37.5rem) 1fr;
		justify-content: center;
	}
}

.site-branding {
	flex-grow: 1;
}
@media screen and (min-width: 50em) {
	.site-branding {
		grid-column: 2/3;
		text-align: left;
	}
}

.site-title {
	margin-bottom: 0;
}

.site-title a, .site-title a:hover, .site-title a:visited {
	color: #fff;
	text-decoration: none;
	font-size: 36px;
}

.site-description {
	color: #fff;
	font-size: 14px;
}

/* Front Page
--------------------------------------------- */
.home-blog {
	margin: 20px;
}

.home-blog h2 {
	font-size: 24px;
	font-weight: bold;
	margin-bottom: 20px;
}

.home-blog article {
	display: inline-flex;
	flex-direction: column;
	margin-bottom: 20px;
	border-bottom: 1px solid #ccc;
	padding-bottom: 20px;
}

.home-blog article:last-child {
	border-bottom: none;
}

.home-blog article h3 a {
	font-size: 20px;
	color: #ffffff;
	text-decoration: none;
}

.home-blog article h3 a:hover {
	color: #ff6347;
}

.home-blog article p {
	font-size: 16px;
	line-height: 1.6;
	margin-bottom: 10px; /* Adjust spacing between title and excerpt */
}

.home-blog article img {
	width: 100%; /* Ensure images take full width of their container */
}

.all-news-btn:hover {
	text-decoration: underline;
}

.blog-post {
	position: relative;
	display: inline-block;
}

.overlay {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%; /* Occupy the entire width of the blog post */
	background-color: rgba(0, 0, 0, 0.5); /* Adjust the opacity as needed */
	color: white;
	padding: 10px;
	box-sizing: border-box; /* Include padding in the width */
}

.overlay h3 {
	margin: 0;
}

/* Adjust styles for smaller screens */
@media screen and (max-width: 768px) {
	.home-blog h2 {
		font-size: 20px;
	}
	.home-blog article {
		width: 100%;
	}
	.home-blog article h3 a {
		font-size: 18px;
	}
	.home-blog article p {
		font-size: 14px;
	}
}
/* Front Page
--------------------------------------------- */
.student-container {
	max-width: 300px;
	margin: 0 auto;
}

.student-container img {
	width: 300px;
	height: 200px;
	object-fit: cover;
}

@media only screen and (min-width: 600px) {
	.student-section {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
	}
	.student-container {
		width: 45%;
		margin: 0 0 1.5rem;
	}
}
@media only screen and (min-width: 900px) {
	.student-container {
		width: 30%;
	}
}
.single-student .wp-post-image.alignright {
	display: block;
	float: none;
	margin-left: 0;
	margin-bottom: 1rem;
}
@media only screen and (min-width: 600px) {
	.single-student .wp-post-image.alignright {
		display: inline;
		float: right;
		margin-left: 1.5rem;
		margin-bottom: none;
	}
}

.single-student ul {
	margin-left: 0;
}
.single-student li {
	list-style-type: none;
	margin: 0;
	padding: 0;
	padding-bottom: 1.5rem;
}

.type-student .wp-post-image.alignleft {
	display: block;
	float: none;
	margin-left: 0;
	margin-bottom: 1rem;
}
@media only screen and (min-width: 600px) {
	.type-student .wp-post-image.alignleft {
		display: inline;
		float: left;
		margin-right: 1.5rem;
		margin-bottom: none;
	}
}

.archive.tax-student_category article {
	clear: both;
	padding-top: 1em;
}
.archive.tax-student_category article:first-of-type {
	padding-top: 0;
}

.portfolio-button {
	box-sizing: border-box;
	display: inline-block;
	text-align: center;
	background-color: #a42c8c;
	border-radius: 500px;
	padding: 0.667rem 1.333rem;
	font-size: 1.15rem;
	color: #fff;
}
.portfolio-button:visited {
	color: #fff;
}

/* Staff Page
--------------------------------------------- */
/* Footer
--------------------------------------------- */
/* Footer container */
#colophon {
	background-color: #343a40; /* Dark grey background */
	padding: 20px;
}

/* Footer logo */
.footer-logo img {
	max-width: 200px; /* Adjust the max-width as needed */
}

/* Footer navigation */
#footer-navigation {
	margin-top: 20px;
}

#footer-navigation ul {
	list-style: none;
	padding: 0;
}

#footer-navigation ul li {
