/* Header styles */
.site-header {
    background-color: #333; 
    color: #fff; 
    padding: 0 20px ; 
    // display: flex;
    // justify-content: space-between;
    // align-items: center;
    // width: 100%; 

    @media screen and (min-width: 50em){
    display: grid;
    grid-template-columns: 1fr minmax(auto, 25rem) minmax(auto, 37.5rem) 1fr;
    justify-content: center;
    }
}

.site-branding {
    flex-grow: 1;
    @media screen and (min-width: 50em) { 
        grid-column: 2 / 3;
        text-align: left;
    }
    
}

.site-title {
	margin-bottom: 0;
}

.site-title a, .site-title a:hover, .site-title a:visited {
    color: #fff; 
    text-decoration: none;
    font-size: 36px; 
   
}

.site-description {
    color: #fff; 
    font-size: 14px; 
}

